.about{
    width: 100%;
    margin:3rem 0;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
    /* border: 1px solid yellow; */

}
.about .left{
    text-align: center;
    margin: auto;
    /* padding: 1rem; */
    max-width:350px ;
}
.about .left p{
    margin: 1.2rem 0;
}
.about .right{
    max-width: 700px;
    /* border: 1px solid red; */

}
.right .img-container{
    display: grid;
    grid-template-columns: repeat(12,1fr);
    position: relative;
    align-items: center;
    text-align: center;
    /* border: 1px solid yellow; */
}
.right .img{
    max-width: 90%;
    border: 1px solid #333;
}
 .right .top{
    grid-column: 1/span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}
.right .bottom{
    grid-column: 4/-1;
    grid-row: 1;
    z-index: 5;
} 