* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Outfit", sans-serif;
  }
  .card_wrapper {
    padding: 4rem 2rem;
    border-radius: 5px;
  }
  .card_container {
    box-shadow: rgba(188, 49, 49, 1) 1px 1px 10px 1px;
    background: rgb(28, 27, 27);
    width: 80%;
    margin: auto;
    padding: 1rem;
    display: grid;
    border-radius: 5px;
    grid-gap: 30px;
    grid-template-columns: repeat(2, 1fr);
  }
  .card_container .img {
    width: 100%;
  }
  p {
    color: white;
  }
  .card_left {
    width: 100%;
    padding: 1rem;
    /* margin-bottom: 20px; */
  }
  .teck_stack {
    color: white;
    text-align: center;
    margin: 1rem 0;
  }
  .tech_stack_icons_wrapper {
    display: flex;
    gap: 20px;
    width: fit-content;
    margin: auto;
  }
  .tech_stack_icon {
    height: 40px;
    width: 40px;
    font-size: 30px;
    box-shadow: rgba(188, 49, 49, 1) 1px 1px 10px 1px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .card_heading {
    color: white;
    text-align: center;
    margin:1rem 0 1rem 0;
    /* border: 1px solid yellow; */
  }
  .card_desc {
    display: flex;
    margin-bottom: 1rem;
  }
  .card_buttons {
    display: flex;
    padding: 1rem;
    justify-content: space-between;
  }
  .point {
    margin-right: 10px;
    display: inline-flex;
    align-items: baseline;
  }
  .card_rigth {
    width: 100%;
  }
  @media screen and (max-width: 1040px) {
    .card_container {
      width: 100%;
    }
    .card_container {
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 20px;
    }
    .card_heading{
      margin:0 0 1rem 0; 
    }
  }
  